<template>
  <div v-if="show" class="fixed inset-0 flex items-center justify-center z-50">
    <div class="flex flex-col border-2 border-bondi-blue-600 border-t-8 bg-white-100 rounded-xl shadow-lg w-5/12 z-50">
      <div class="font-bold text-lg text-abbey-500 border-b-2 p-4 px-6 space-y-4 border-bondi-blue-600">
        Register For Event
      </div>
      <div v-if="message" class="p-4 m-6 m-y-4 rounded-xl text-mountain-meadow-800 py-2 text bg-spray-600">
        {{ message }}
      </div>
      <form class="flex flex-col space-y-4 p-4 px-6" @submit.prevent="registerForEvent" method="POST">
        <SpinnerComponent v-if="isLoading" />
        <label class="block">
          <span class="after:content-['*'] after:ml-0.5 after:text-red-500 block text-sm font-medium text-slate-700">
            Participation role
          </span>
          <select name="participant_category"
            class="mt-2 px-3 py-3 bg-white border border-1 shadow-sm border-abbey-400 placeholder-slate-400 focus:outline-none focus:border-sky-500 focus:ring-sky-500 block w-full rounded-2xl sm:text-sm focus:ring-1"
            v-model="eventData.participant_category" required>
            <option value="" disabled selected>--Select your role--</option>
            <option value="Participant">Participant</option>
            <option value="Presenter">Presenter</option>
            <option value="Organiser">Organiser</option>
          </select>
        </label>
        <div class="flex flex-row space-x-4">
          <button type="submit" class="mt-2 px-4 py-2 text-white-100 bg-bondi-blue hover:bg-bondi-blue-400 rounded-2xl">
            Register
          </button>
          <button @click="close"
            class="mt-2 px-4 py-2 boder border-2 border-abbey-400 hover:border-abbey-200 rounded-2xl">Close</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { createItem } from "@/services/apiService";
import SpinnerComponent from "@/components/Spinner.vue";

export default {
  components: {
    SpinnerComponent
  },
  data() {
    return {
      eventData: {
        event_id: "",
        participant_category: "",
      },
      isLoading: false,
      message: "",
    };
  },


  props: {
    show: {
      type: Boolean,
      required: true,
    },
    event_id: {
      type: Number,
      required: true,
    },
  },

  methods: {
    async registerForEvent() {
      this.eventData.event_id = this.event_id
      this.isLoading = true;
      try {
        const response = await createItem("events/register/", this.eventData);
        this.participants = response.data;
        this.isLoading = false;
        this.$emit("registered")
        this.message = "Successfully registered for an event";
      } catch (error) {
        console.error("Error regsitering for event:", error);
        this.isLoading = false;
      }
    },
    close() {
      this.$emit("closed");
    },
  },
};
</script>
